
#repo-list-page{padding-top:25px;background-color:#f5f5f5;position:absolute;bottom:0;right:0;left:0
}
#repo-list-page #search-wrapper{position:absolute;top:20px;left:15px;right:15px;z-index:2;border-radius:8px;transform:translate3d(0, 0, 0);transition:box-shadow .3s,transform .3s
}
#repo-list-page #search-wrapper.shadow{box-shadow:0 0 20px rgba(0,0,0,0.3);transform:translate3d(0, -12px, 0)
}
#repo-list-page #scroll-wrapper{position:absolute;top:0;left:0;right:0;bottom:0;overflow-y:scroll;overflow-x:hidden;-webkit-overflow-scrolling:touch;z-index:1
}
#repo-list-page #search-input input{border:solid .5px #e5e5e5
}
#repo-list-page #repo-list{margin-top:75px;margin-left:15px;margin-right:15px;margin-bottom:15px;min-height:100vh
}
#repo-list-page #load-more{width:165px;height:40px;margin:30px auto;line-height:40px;border-radius:8px;border:solid .5px #aeb6c0;font-family:GothamPro-Bold;font-size:14px;letter-spacing:.5px;text-align:center;color:#8f9aa8
}
#repo-list-page.transition-appear{transform:scale(0);opacity:.01;transition:transform .3s cubic-bezier(.7, 0, .25, 1),box-shadow .3s cubic-bezier(.7, 0, .25, 1);height:100vh
}
#repo-list-page.transition-appear.transition-appear-active{transform:scale(1);opacity:1
}


