
#repo-detail{min-height:100vh;overflow:hidden;background-color:#f5f5f5
}
#repo-detail.transition-appear{transition:transform .3s,top .3s,left .3s,right .3s,width .3s,bottom .3s,width .3s,height .3s
}
.tab-wrapper-transition-group{height:50px;display:block
}
#repo-tabs-wrapper{height:50px;overflow:hidden;border-bottom:solid .5px #e5e5e5;z-index:2
}
#repo-tabs-wrapper.fixed{position:fixed;top:60px;right:0;left:0
}
#repo-tabs{height:70px;background-color:#f6f7f8;overflow-x:scroll;overflow-y:hidden;white-space:nowrap;-webkit-overflow-scrolling:touch
}
#repo-tabs .repo-tab-item{font-family:GothamPro;font-size:13px;height:50px;line-height:50px;letter-spacing:.3px;text-align:center;color:#8f9aa8;display:inline-block;padding:0 10px;position:relative;margin:0 5px
}
#repo-tabs .repo-tab-item:first-child{margin-left:15px
}
#repo-tabs .repo-tab-item:last-child{margin-right:15px
}
#repo-tabs .repo-tab-item:after{transform:scaleX(0);transition:transform .3s;transform-origin:left center;content:'';display:block;position:absolute;left:0;bottom:0;width:100%;height:3px;background-color:#8f9aa8
}
#repo-tabs .repo-tab-item.selected:after{transform:scaleX(1)
}
#repo-tab-content{position:relative;overflow:hidden;z-index:1
}
#repo-tab-content .repo-content-item{padding:15px;opacity:0;transform:translate3d(0, 100px, 0);transition:opacity .5s,transform .5s;position:absolute;right:15px;left:15px;top:15px
}
#repo-tab-content .repo-content-item.show{opacity:1;transform:translate3d(0, 0, 0);z-index:99
}
#repo-tab-content #readme,#repo-tab-content #files,#repo-tab-content #languages{border-radius:6px;background-color:#ffffff;border:solid 1px #e5e5e5
}
.file-item{display:-ms-flexbox;display:flex;color:#8f9aa8;margin-bottom:19px
}
.file-item:last-child{margin-bottom:0
}
.file-item .file-icon{margin-right:12px
}
.file-item .file-name{font-family:GothamPro;font-size:14px;line-height:1.5;margin-bottom:8px
}
.file-item .file-date{font-family:GothamPro;font-size:13px;color:#aeb6c0
}
.contrib-item{display:-ms-flexbox;display:flex;color:#8f9aa8;margin-bottom:5px;border-radius:6px;background-color:#ffffff;border:solid .5px #e5e5e5;padding:15px
}
.contrib-item:last-child{margin-bottom:0
}
.contrib-item .contrib-avatar{margin-right:15px;width:40px;height:40px;background-position:center center;background-size:cover;border-radius:5px
}
.contrib-item .contrib-name{font-family:GothamPro-Medium;font-size:18px;letter-spacing:-0.2px;color:#5c6877;margin-bottom:11px
}
.contrib-item .contrib-value{font-family:GothamPro;font-size:13px;color:#aeb6c0
}
.lang-item{display:-ms-flexbox;display:flex;color:#8f9aa8;margin-bottom:19px;-ms-flex-align:center;align-items:center
}
.lang-item:last-child{margin-bottom:0
}
.lang-item .lang-color{margin-right:20px;width:32px;height:32px;border-radius:16px
}
.lang-item .lang-name{font-family:GothamPro-Bold;font-size:15px;line-height:1.4;color:#5c6877;margin-bottom:5px
}
.lang-item .lang-date{font-family:GothamPro;font-size:13px;color:#aeb6c0
}
.empty-data{text-align:center;font-size:15px;color:#ccc
}
.offline-msg{margin:20px 0;text-align:center;font-size:15px;color:#ccc
}
.offline-msg .blue-link{color:#64b5f6;font-weight:bold;margin:10px;text-transform:uppercase
}


