
.header{display:-ms-flexbox;display:flex;height:60px;-ms-flex-pack:justify;justify-content:space-between;width:100%;position:relative;z-index:2;background-color:#0f2035;transform:translate3d(0, 0, 0);box-shadow:0 3px 5px 0 rgba(15,31,52,0.4);transition:background-color .3s
}
.header.transparent{background-color:#224365;box-shadow:none
}
.header #hamberger-menu{height:60px;width:50px
}
.header #notification-icon{background-image:url('../assets/notification-icon.png');background-position:center center;background-repeat:no-repeat;background-size:15px 17px;height:60px;width:50px
}
.header #brand-logo{display:block;background-image:url('../assets/github-logo.png');background-position:center center;background-repeat:no-repeat;background-size:74px 20px;height:60px;width:74px
}


