
#loading-block{height:3px;width:100%;background-color:#46a42f;transform:scaleX(0);z-index:3;position:absolute;transform-origin:0 0
}
#loading-block.failed{background-color:#f44336;transform:scaleX(1) !important;transition:transform .3s !important
}
#loading-block.one{transform:scaleX(.2);transition:transform .5s
}
#loading-block.two{transform:scaleX(.8);transition:transform 20s cubic-bezier(0, 1.17, 1, 1)
}
#loading-block.three{transform:scaleX(1);transition:transform .3s
}


