
html{overflow:hidden
}
body{font-family:GothamPro;-webkit-font-smoothing:antialiased;overflow:hidden;background-color:#3d536d;margin:0
}
h1,h2,h3,h4{font-weight:500
}
a{text-decoration:none
}
.round-btn{width:120px;height:32px;border-radius:50px;border:solid .5px #7ed321;font-family:GothamPro-Bold;font-size:14px;text-align:center;color:#ffffff;text-transform:uppercase;line-height:32px
}
.green-btn{display:inline-block;width:245px;height:48px;line-height:48px;border-radius:8px;background-image:linear-gradient(to bottom, #a5de41, #55b130);box-shadow:inset 0 .5px .5px 0 #e2ffce;font-family:GothamPro-Bold;font-size:14px;letter-spacing:.5px;text-align:center;color:#ffffff !important;text-decoration:none !important
}
#menu-overlay{position:fixed;top:0;right:0;left:84%;bottom:0;z-index:3;transform:translate3d(1000px, 0, 0)
}
.list-appear,.list-enter{opacity:.01;transition:opacity .8s,transform .5s
}
.list-appear:nth-child(0),.list-enter:nth-child(0){transform:translate3d(0, 100px, 0)
}
.list-appear:nth-child(1),.list-enter:nth-child(1){transform:translate3d(0, 200px, 0)
}
.list-appear:nth-child(2),.list-enter:nth-child(2){transform:translate3d(0, 300px, 0)
}
.list-appear:nth-child(3),.list-enter:nth-child(3){transform:translate3d(0, 400px, 0)
}
.list-appear:nth-child(4),.list-enter:nth-child(4){transform:translate3d(0, 500px, 0)
}
.list-appear:nth-child(5),.list-enter:nth-child(5){transform:translate3d(0, 600px, 0)
}
.list-appear:nth-child(6),.list-enter:nth-child(6){transform:translate3d(0, 700px, 0)
}
.list-appear:nth-child(7),.list-enter:nth-child(7){transform:translate3d(0, 700px, 0)
}
.list-appear:nth-child(8),.list-enter:nth-child(8){transform:translate3d(0, 800px, 0)
}
.list-appear:nth-child(9),.list-enter:nth-child(9){transform:translate3d(0, 900px, 0)
}
.list-appear:nth-child(10),.list-enter:nth-child(10){transform:translate3d(0, 1000px, 0)
}
.list-appear.list-appear-active,.list-enter.list-enter-active{opacity:1;transform:translate3d(0, 0, 0) !important
}
.list-leave-active{opacity:0
}
.trasition-wrapper{position:relative;min-height:100%;z-index:1
}
.trasition-wrapper .transition-item{position:absolute;top:0;left:0;right:0;z-index:99
}


