
#main-content{will-change:transform;transition:transform .3s cubic-bezier(.7, 0, .25, 1),box-shadow .3s cubic-bezier(.7, 0, .25, 1);box-shadow:-3px 0 5px 0 rgba(15,31,52,0.4);position:absolute;top:0;height:100vh;width:100%;background-color:#0f2035
}
#main-content #scroll-section{position:absolute;overflow-y:scroll;overflow-x:hidden;-webkit-overflow-scrolling:touch;top:60px;left:0;right:0;bottom:0
}


